@import url('https://fonts.googleapis.com/css?family=Anton&display=swap');

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes drop {
  0% {
    bottom: 0px;
    opacity: 1;
  }
  
  80% {
    opacity: 1;
  }
  
  100% {
    opacity: 1;
    bottom: -295px;
  }
}

@keyframes wave {
  0% {
    background-position: 0 160px;
    background-size: 170px 300px;
  }
  
  100% {
    background-position: 500px -18px;
    background-size: 250px 150px;
    opacity: 0;
  }
}

.kuhl{
  margin-top: 5px;
  width: 88%;
}

.loader {
  width: 120px;
  height: 120px;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  text-align: center;
  line-height: 120px;
  font-family: sans-serif;
  color: #ffffff;
  font-size: 16px;
  border-radius: 25px;
}

.loader span {
  z-index: 3;
}

.loader-bg {
  border-radius: 25px;
  background: transparent repeat-x 0 100px/150px 300px url('data:image/png;base64,'); 
  z-index: 2;
  animation: wave 1s ease-out forwards;
}

.drops {
  -webkit-filter: url('#liquid');
  filter: url('#liquid');
  z-index: 1;
  opacity: 0;
  animation: fade-in .1s linear .4s forwards;
  z-index: 0;
  margin-top: -29px;
}

.drop1 , .drop2 {
  width: 21px;
  height: 24px;
  border-radius: 50%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: #293347
}

.drop1 {
  width: 75px;
  height: 16px;
  bottom: 2px;
  border-radius: 1;
}

.drop2 {
  animation: drop 1.6s cubic-bezier(1,.19,.66,.12) .5s infinite;
}



#container{
  margin: 269px auto;
  position: relative;
  filter: url(#goo);
}

  .text{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-family: Anton;
    font-size: 200px;
    z-index: 999;
    color: #293347;
  }


  .underDrop{
    height: 30px;
    width: 30px;
    border-radius: 15px;
    background-color: #293347;
    position: absolute;
    top: 50%;
    left: 46%;
    transform: translate(120%,-800%) scaleX(0.5);
    animation: move 6s cubic-bezier(1,.04,.74,.2) infinite;
  }



@keyframes move{
  0%{
    transform: translate(120%,-500%) scaleX(0.2);
    height: 60px;
  }
  30%{
    transform: translate(120%,-200%);
    height: 30px;
  }
  70%{
    transform: translate(120%,100%);
    height: 30px;
  }
  100%{
    transform: translate(120%,800%) scaleX(0.2);
    height: 60px;
  }
}





/* About */
.about-container {
  display: flex;
  /* height: 70vh; */
  width: 100%;
  align-items: center;
  justify-content: center;
  background-color: #7510F7;
}



.about-button {
  position: absolute;
  animation: ripple 1.6s infinite;
  border-radius: 100%;
  margin-bottom: 450px;
  border: none;
  background-color: #eff2f5;
  box-shadow: inset 10px 10px 20px #a5a7a9, inset -10px -10px 20px #7510F7;
  transition: 0.1s ease-in all;
  opacity: 0;
  z-index: -1;
}

